.ant-btn {
  display: flex;
  align-items: center;
  justify-content: center;
}
.ant-btn svg {
  margin-right: 10px;
}
.ant-btn.center {
  margin-left: auto;
  margin-right: auto;
}
.ant-btn.ant-btn-default {
  background: transparent;
  border: 1px solid #0053FA;
  border-radius: 2px;
  font-weight: 600;
  font-size: 11px;
  color: #0053FA;
  letter-spacing: 0;
  text-align: center;
  padding: 9px 14px 7px 14px;
  line-height: 12px;
  flex-direction: row;
  display: flex;
}
.ant-btn.ant-btn-danger {
  color: #FFFFFF;
  background: #FF5F5F;
  border: #FF5F5F;
}
.ant-btn.ant-btn-primary {
  text-decoration: none;
  font-weight: 600;
  font-size: 11px;
  color: #FFFFFF;
  letter-spacing: 0;
  text-align: center;
  line-height: 11px;
  padding: 9px 14px 7px 14px;
  border-radius: 2px;
  box-shadow: 0 11px 15px -10px rgba(0, 83, 250, 0.25);
  flex-direction: row;
  display: flex;
}
.ant-btn.ant-btn-primary:hover {
  box-shadow: 0 10px 10px -10px rgba(0, 83, 250, 0.25);
}
.ant-btn.ant-btn-ghost {
  text-align: center;
  line-height: 11px;
  padding: 9px 14px 7px 14px;
  border-radius: 0px;
  box-shadow: none;
  background: transparent;
  border: 0px;
  text-transform: inherit;
  font-weight: 400;
  font-size: 16px;
  color: #0053FA;
  letter-spacing: 0;
  text-decoration: underline;
}
.ant-btn.ant-btn-ghost:hover {
  box-shadow: none;
}
.ant-btn.disabled,
.ant-btn[disabled],
.ant-btn.disabled:hover,
.ant-btn[disabled]:hover,
.ant-btn.disabled:focus,
.ant-btn[disabled]:focus,
.ant-btn.disabled:active,
.ant-btn[disabled]:active,
.ant-btn.disabled.active,
.ant-btn[disabled].active {
  border-color: #989CA8 !important;
  color: #989CA8 !important;
}
