.sub-heading {
  white-space: pre-wrap;
  flex: 1;
}
.sub-heading H2 {
  white-space: pre-wrap;
  font-weight: 700;
  font-size: 16px;
  color: #0053FA;
  letter-spacing: 0;
  margin-bottom: 20px;
}
.sub-heading-right svg {
  margin-right: 10px;
}
.sub-heading-right a {
  display: flex;
  align-items: center;
  text-decoration: underline;
  color: #0053FA;
}
.sub-heading-right a:hover {
  color: #0053FA;
}
