.content-secondary {
  background: #0053FA;
  margin: 0;
  min-height: 100%;
  width: 100%;
  padding: 24px 24px 0;
  color: white;
  display: flex;
  flex-direction: column;
}
.content-secondary .ant-btn.ant-btn-primary {
  color: #0053FA;
  background: white;
}
.content-secondary .H1 {
  color: white;
}
.content-secondary .heading-description {
  color: white;
}
.content-secondary .form-item-text.active .ant-form-item-label:before,
.content-secondary .form-item-password.active .ant-form-item-label:before,
.content-secondary .form-item-date.active .ant-form-item-label:before,
.content-secondary .form-item-select.active .ant-form-item-label:before,
.content-secondary .search-autocomplete .form-item-search.active .ant-form-item-label:before,
.content-secondary .form-item-email.active .ant-form-item-label:before {
  content: ' ';
  background: #0053FA;
}
.content-secondary .form-item-text.active .ant-form-item-label label,
.content-secondary .form-item-password.active .ant-form-item-label label,
.content-secondary .form-item-date.active .ant-form-item-label label,
.content-secondary .form-item-select.active .ant-form-item-label label,
.content-secondary .search-autocomplete .form-item-search.active .ant-form-item-label label,
.content-secondary .form-item-email.active .ant-form-item-label label,
.content-secondary .form-item-text.active .ant-form-item-required:before,
.content-secondary .form-item-password.active .ant-form-item-required:before,
.content-secondary .form-item-date.active .ant-form-item-required:before,
.content-secondary .form-item-select.active .ant-form-item-required:before,
.content-secondary .search-autocomplete .form-item-search.active .ant-form-item-required:before,
.content-secondary .form-item-email.active .ant-form-item-required:before {
  color: white;
}
.content-secondary .form-item .ant-form-item-label label {
  color: rgba(255, 255, 255, 0.5);
}
.content-secondary .form-item .ant-form-item-label label:before {
  color: rgba(255, 255, 255, 0.5);
}
.content-secondary .ant-input {
  border-color: white !important;
}
.content-secondary .ant-btn-primary {
  border-color: white;
  box-shadow: none;
}
.content-secondary .floating-action-footer .ant-btn-primary {
  border: 1px solid #0053FA !important;
  border-color: white;
  box-shadow: none;
}
.content-secondary .ant-btn-default:not([disabled]) {
  background-color: #0053FA;
  color: white;
  box-shadow: 0 11px 15px -10px rgba(0, 83, 250, 0.25);
}
.content-secondary .ant-calendar-picker-input.ant-input,
.content-secondary .ant-input-affix-wrapper .ant-input,
.content-secondary .ant-select .ant-select-selection {
  color: white;
  border-color: white;
}
.content-secondary .ant-select-arrow svg {
  color: white;
}
.content-secondary .ant-calendar-picker-icon {
  color: rgba(255, 255, 255, 0.25);
}
.content-secondary .ant-checkbox-wrapper,
.content-secondary .ant-checkbox-wrapper .ant-radio,
.content-secondary .ant-radio-wrapper,
.content-secondary .ant-radio-wrapper .ant-radio {
  color: white;
}
.content-secondary .ant-checkbox.ant-checkbox-checked .ant-checkbox-inner {
  background-color: white;
  color: #0053FA;
}
.content-secondary .ant-checkbox-checked .ant-checkbox-inner:after {
  border-color: #0053FA;
}
.content-secondary .has-error .ant-form-explain {
  color: white;
}
.content-secondary input:-webkit-autofill,
.content-secondary input:-webkit-autofill:hover,
.content-secondary input:-webkit-autofill:focus,
.content-secondary input:-webkit-autofill:active {
  box-shadow: 0 0 0 30px #0053FA inset !important;
  -webkit-text-fill-color: white !important;
}
