.kyc-layout * {
  overflow: -moz-scrollbars-none;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
.kyc-layout *::-webkit-scrollbar {
  width: 0 !important;
}
.content-primary {
  flex: none;
  min-height: calc(36vh);
  background: #ffffff;
  margin: 0;
  flex: 1;
  padding: 30px 24px 24px;
}
.content-primary.content-with-footer {
  min-height: calc(-19vh);
}
.ant-layout.ant-layout-has-sider {
  min-height: calc(36vh);
}
.RightSideBar_sidebar-right {
  margin: 0;
  background: #F7F9FB;
  box-shadow: inset 1px 0 0 0 #DFDFDF;
  overflow: hidden;
  position: sticky;
  top: 64px;
  height: calc(36vh);
  overflow: auto;
}
.RightSideBar_sidebar-right.close {
  width: 0px;
}
.RightSideBar_sidebar-right-inner {
  padding: 44px 16px 24px 16px;
  position: relative;
}
.RightSideBar_sidebar-right.close .RightSideBar_sidebar-right-inner {
  transform: translate(-100%, 0);
}
.RightSideBar_sidebar-right.open .RightSideBar_sidebar-right-inner {
  transform: translate(0, 0);
}
.close-sidebar-right {
  position: absolute;
  width: 60px;
  height: 50px;
  right: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px 25px 10px 10px;
  cursor: pointer;
  z-index: 9;
}
.floating-action-footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: 55px;
  background: #FFFFFF;
  box-shadow: 0 -1px 0 0 #DFDFDF;
  z-index: 10;
  display: flex;
  align-items: center;
  padding: 0 15px;
}
@media (max-width: 767px) {
  .content-primary {
    overflow: hidden;
  }
}
.content-primary input:-webkit-autofill,
.content-primary input:-webkit-autofill:hover,
.content-primary input:-webkit-autofill:focus,
.content-primary input:-webkit-autofill:active {
  box-shadow: 0 0 0 30px white inset !important;
  -webkit-text-fill-color: #0053FA !important;
}
.content-reverse {
  background: #0053FA;
  margin: 0;
  min-height: 100%;
  width: 100%;
  padding: 24px 24px 0;
  color: white;
  display: flex;
  flex-direction: column;
}
.content-reverse .ant-btn.ant-btn-primary {
  color: #0053FA;
  background: white;
}
.content-reverse .heading .H1 {
  color: white;
}
.content-reverse .heading-description {
  color: #b3cbfd;
}
.content-reverse .form-item-text.active .ant-form-item-label:before,
.content-reverse .form-item-password.active .ant-form-item-label:before,
.content-reverse .form-item-date.active .ant-form-item-label:before,
.content-reverse .form-item-select.active .ant-form-item-label:before,
.content-reverse .search-autocomplete .form-item-search.active .ant-form-item-label:before,
.content-reverse .form-item-email.active .ant-form-item-label:before {
  content: ' ';
  background: #0053FA;
}
.content-reverse .form-item-text.active .ant-form-item-label label,
.content-reverse .form-item-password.active .ant-form-item-label label,
.content-reverse .form-item-date.active .ant-form-item-label label,
.content-reverse .form-item-select.active .ant-form-item-label label,
.content-reverse .search-autocomplete .form-item-search.active .ant-form-item-label label,
.content-reverse .form-item-email.active .ant-form-item-label label,
.content-reverse .form-item-text.active .ant-form-item-required:before,
.content-reverse .form-item-password.active .ant-form-item-required:before,
.content-reverse .form-item-date.active .ant-form-item-required:before,
.content-reverse .form-item-select.active .ant-form-item-required:before,
.content-reverse .search-autocomplete .form-item-search.active .ant-form-item-required:before,
.content-reverse .form-item-email.active .ant-form-item-required:before {
  color: white;
}
.content-reverse .form-item .ant-form-item-label label {
  color: rgba(255, 255, 255, 0.5);
}
.content-reverse .form-item .ant-form-item-label label:before {
  color: rgba(255, 255, 255, 0.5);
}
.content-reverse .ant-input {
  border-color: white !important;
}
.content-reverse .ant-btn-primary {
  border-color: white;
  box-shadow: none;
}
.content-reverse .floating-action-footer .ant-btn-primary {
  border: 1px solid #0053FA !important;
  border-color: white;
  box-shadow: none;
}
.content-reverse .ant-btn-default:not([disabled]) {
  background-color: #0053FA;
  color: white;
  box-shadow: 0 11px 15px -10px rgba(0, 83, 250, 0.25);
}
.content-reverse .ant-calendar-picker-input.ant-input,
.content-reverse .ant-input-affix-wrapper .ant-input,
.content-reverse .ant-select .ant-select-selection {
  color: white;
  border-color: white;
}
.content-reverse .ant-select-arrow svg {
  color: white;
}
.content-reverse .ant-calendar-picker-icon {
  color: rgba(255, 255, 255, 0.25);
}
.content-reverse .ant-checkbox-wrapper,
.content-reverse .ant-checkbox-wrapper .ant-radio,
.content-reverse .ant-radio-wrapper,
.content-reverse .ant-radio-wrapper .ant-radio {
  color: white;
}
.content-reverse .ant-checkbox.ant-checkbox-checked .ant-checkbox-inner {
  background-color: white;
  color: #0053FA;
}
.content-reverse .ant-checkbox-checked .ant-checkbox-inner:after {
  border-color: #0053FA;
}
.content-reverse .has-error .ant-form-explain {
  color: white;
}
.content-reverse input:-webkit-autofill,
.content-reverse input:-webkit-autofill:hover,
.content-reverse input:-webkit-autofill:focus,
.content-reverse input:-webkit-autofill:active {
  box-shadow: 0 0 0 30px #0053FA inset !important;
  -webkit-text-fill-color: white !important;
}
