.sidebar {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 27px;
  margin: 0 !important;
  height: inherit;
  height: auto;
  overflow: visible;
  position: relative;
  overflow: auto;
}
.sidebar .ant-divider-horizontal {
  margin: 0;
}
.sidebar .toggle-sidebar {
  cursor: pointer;
  height: 20px;
  position: absolute;
  right: 10px;
  top: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 !important;
  padding: 0 !important;
  justify-content: flex-end;
  font-weight: 400;
  font-size: 12px;
  color: #B5B9C5;
  letter-spacing: 0;
}
.sidebar .toggle-sidebar svg {
  width: 10px;
  height: 10px;
  margin-right: 5px;
}
.sidebar.sticky {
  position: sticky;
  top: 64px;
  height: calc(36vh);
}
.sidebar.stripe .ant-menu-sub .ant-menu-item,
.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item {
  padding-bottom: 15px !important;
}
.sidebar.stripe .ant-menu-sub .ant-menu-item:first-child:before,
.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:first-child:before {
  height: 12px;
  top: 0px;
}
.sidebar.stripe .ant-menu-sub .ant-menu-item:before,
.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:before {
  content: ' ';
  position: absolute;
  width: 1px;
  background-color: #B5B9C5;
  left: 35px;
  height: 40px;
  top: -27px;
}
.sidebar.stripe .ant-menu-sub .ant-menu-item:after,
.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:after {
  content: ' ';
  position: absolute;
  height: 1px;
  width: 10px;
  background-color: #B5B9C5;
  left: 35px;
  top: 12px;
  opacity: 1;
  right: inherit;
  transform: none;
  border: 0;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu {
  padding: 0 0 0 10px;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item {
  padding-left: 15px !important;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item .ant-menu-item-wrapper {
  padding: 1px 0px 1px 10px;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:before {
  left: 0;
  height: 26px;
  top: -13px;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:after {
  left: 0;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:last-child:after {
  padding-bottom: 0px !important;
}
.ant-menu-submenu.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item:first-child:before {
  display: none;
}
.sidebar-collapsed .ant-menu-submenu-selected .ant-menu-item-wrapper:after,
.ant-menu-item-selected .ant-menu-item-wrapper:after {
  content: ' ';
  position: absolute;
  right: 0;
  top: 0;
  border-right: 3px solid #0053FA;
  margin-right: 0px;
  width: 3px;
  height: 100%;
}
.sidebar.contrast .ant-menu-submenu,
.sidebar.contrast .ant-menu-item {
  color: #0d1b2a;
}
.sidebar.contrast .ant-menu-sub {
  background: #F5F6FA;
}
.sidebar.contrast svg {
  color: #0053FA;
}
.sidebar.contrast .reverse svg {
  color: white;
}
.sidebar.contrast .ant-menu-sub .ant-menu-item:before {
  content: ' ';
  position: absolute;
  height: 1px;
  width: 5px;
  background-color: #0053FA;
  top: 50%;
  opacity: 1;
  right: inherit;
  transform: none;
  border: 0;
}
.sidebar > .ant-menu,
.ant-menu-submenu-popup > .ant-menu {
  padding: 30px 0 0 0;
  height: 100%;
}
.sidebar.collapsable-false > .ant-menu,
.ant-menu-submenu-popup.collapsable-false > .ant-menu {
  padding: 0;
}
.sidebar svg,
.ant-menu-submenu-popup svg {
  margin-right: 9px;
  width: 16px;
  height: 16px;
  vertical-align: middle;
  color: inherit;
}
.sidebar .ant-menu-submenu-arrow:after,
.ant-menu-submenu-popup .ant-menu-submenu-arrow:after,
.sidebar .ant-menu-submenu-arrow:before,
.ant-menu-submenu-popup .ant-menu-submenu-arrow:before {
  transition: background-color 0.3s ease-out !important;
  background: #B5B9C5;
}
.sidebar .ant-menu-submenu-selected,
.ant-menu-submenu-popup .ant-menu-submenu-selected {
  color: #0053FA;
}
.sidebar .ant-menu-submenu-selected .ant-menu-submenu-arrow:after,
.ant-menu-submenu-popup .ant-menu-submenu-selected .ant-menu-submenu-arrow:after,
.sidebar .ant-menu-submenu-selected .ant-menu-submenu-arrow:before,
.ant-menu-submenu-popup .ant-menu-submenu-selected .ant-menu-submenu-arrow:before {
  background: #0053FA;
}
.sidebar .ant-menu-item,
.ant-menu-submenu-popup .ant-menu-item {
  line-height: 39px;
  color: #B5B9C5;
  transition: color 0.3s ease-out !important;
  padding: 0 !important;
  margin: 0 !important;
  margin-right: 3px;
}
.sidebar .ant-menu-item .disabled,
.ant-menu-submenu-popup .ant-menu-item .disabled {
  display: none;
  color: #B5B9C5 !important;
}
.sidebar .ant-menu-item .ant-menu-item-wrapper,
.ant-menu-submenu-popup .ant-menu-item .ant-menu-item-wrapper {
  padding: 1px 20px 1px;
}
.sidebar .ant-menu-item .ant-menu-item-wrapper.reverse,
.ant-menu-submenu-popup .ant-menu-item .ant-menu-item-wrapper.reverse {
  background: #0053FA;
  color: white;
}
.sidebar .ant-menu-item .ant-menu-item-wrapper > *,
.ant-menu-submenu-popup .ant-menu-item .ant-menu-item-wrapper > * {
  transition: none;
  color: inherit;
}
.sidebar .ant-menu-item.ant-menu-item-selected,
.ant-menu-submenu-popup .ant-menu-item.ant-menu-item-selected,
.sidebar .ant-menu-item:hover,
.ant-menu-submenu-popup .ant-menu-item:hover {
  color: #0053FA;
  background: transparent;
}
.sidebar .ant-menu-item.ant-menu-item-selected .ant-menu-submenu-arrow:after,
.ant-menu-submenu-popup .ant-menu-item.ant-menu-item-selected .ant-menu-submenu-arrow:after,
.sidebar .ant-menu-item:hover .ant-menu-submenu-arrow:after,
.ant-menu-submenu-popup .ant-menu-item:hover .ant-menu-submenu-arrow:after,
.sidebar .ant-menu-item.ant-menu-item-selected .ant-menu-submenu-arrow:before,
.ant-menu-submenu-popup .ant-menu-item.ant-menu-item-selected .ant-menu-submenu-arrow:before,
.sidebar .ant-menu-item:hover .ant-menu-submenu-arrow:before,
.ant-menu-submenu-popup .ant-menu-item:hover .ant-menu-submenu-arrow:before {
  background: #0053FA;
}
.sidebar .ant-menu-sub .ant-menu-item,
.ant-menu-submenu-popup .ant-menu-sub .ant-menu-item {
  padding-left: 54px !important;
  height: 40px;
  font-weight: 400;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 23px !important;
  text-align: left;
  height: inherit;
  margin: 0 !important;
  padding: 0 15px 0 30px !important;
  position: relative;
  overflow: visible;
}
.sidebar .sidebar-item,
.ant-menu-submenu-popup .sidebar-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
}
.sidebar .sidebar-item .sidebar-item-left,
.ant-menu-submenu-popup .sidebar-item .sidebar-item-left {
  flex: 1;
  text-align: left;
}
.sidebar.sidebar-collapsed,
.ant-menu-submenu-popup.sidebar-collapsed {
  width: 50px;
}
.sidebar.sidebar-collapsed .ant-menu-item-wrapper,
.ant-menu-submenu-popup.sidebar-collapsed .ant-menu-item-wrapper {
  padding: 1px 20px 0;
}
.sidebar.sidebar-collapsed .ant-menu-item-wrapper > *,
.ant-menu-submenu-popup.sidebar-collapsed .ant-menu-item-wrapper > * {
  width: 20px;
  overflow: hidden;
  display: block;
}
.sidebar.sidebar-collapsed .toggle-sidebar span,
.ant-menu-submenu-popup.sidebar-collapsed .toggle-sidebar span {
  display: none;
}
.sidebar.sidebar-collapsed .toggle-sidebar svg,
.ant-menu-submenu-popup.sidebar-collapsed .toggle-sidebar svg {
  transform: rotate(180deg);
}
.sidebar.sidebar-collapsed .ant-menu,
.ant-menu-submenu-popup.sidebar-collapsed .ant-menu {
  width: 50px;
}
.sidebar.sidebar-collapsed .ant-menu > .ant-menu-item,
.ant-menu-submenu-popup.sidebar-collapsed .ant-menu > .ant-menu-item,
.sidebar.sidebar-collapsed .ant-menu > .ant-menu-submenu > .ant-menu-submenu-title,
.ant-menu-submenu-popup.sidebar-collapsed .ant-menu > .ant-menu-submenu > .ant-menu-submenu-title {
  padding: 0 !important;
}
.ant-menu-inline-collapsed-tooltip .ant-tooltip-arrow {
  border-color: white !important;
}
.ant-menu-inline-collapsed-tooltip .ant-tooltip-inner {
  background: white;
}
.ant-menu-inline-collapsed-tooltip .ant-tooltip-inner .sidebar-item {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0;
  line-height: 27px;
  color: #0053FA;
}
.ant-menu-inline-collapsed-tooltip .ant-tooltip-inner .sidebar-item svg {
  margin-right: 9px;
  width: 16px;
  height: 16px;
  vertical-align: middle;
  color: inherit;
}
