.alert-custom.ant-alert {
  border: 0 !important;
  border-radius: 0 !important;
  font-weight: 500;
  font-size: 16px;
  color: #FFFFFF !important;
  letter-spacing: 0;
  margin-bottom: 20px;
  padding: 2px 15px;
}
.alert-custom.ant-alert.ant-alert-no-icon {
  padding: 2px 15px;
}
.alert-custom.ant-alert.floating {
  position: absolute;
  left: 0;
  width: 100%;
  z-index: 9;
  margin-bottom: 0;
}
.alert-custom.ant-alert .ant-alert-success {
  background: transparent;
}
.alert-custom.ant-alert .ant-alert-success .ant-alert-message {
  color: #5DBE42;
}
.alert-custom.ant-alert .ant-alert-success:not(.background),
.alert-custom.ant-alert .ant-alert-error:not(.background) {
  padding: 15px 0;
  background: transparent;
}
.ant-alert .ant-alert-message a {
  text-decoration: underline;
}
.ant-alert.background .ant-alert-message {
  font-weight: 500;
  font-size: 14px;
  color: #FFFFFF !important;
  letter-spacing: 0;
}
.ant-alert.ant-alert-success .ant-alert-message {
  color: #5DBE42;
}
.ant-alert.ant-alert-success.background {
  background: #5DBE42 !important;
}
.ant-alert.ant-alert-error .ant-alert-message {
  color: #F0455A;
}
.ant-alert.ant-alert-error.background {
  background: #F0455A !important;
}
.ant-alert .ant-alert-close-icon {
  width: 40px;
  height: 100%;
  border-radius: 20px;
  text-align: center;
  top: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.ant-alert .ant-alert-close-icon .anticon-close {
  color: white !important;
}
