.popover-invoice-icon {
  padding: 0 10px;
  cursor: pointer;
}
.ant-table-tbody .popover-invoice-icon {
  opacity: 0;
  cursor: pointer;
}
.ant-table-tbody .popover-invoice-icon svg {
  vertical-align: middle;
}
.ant-table-tbody .popover-invoice-icon.open {
  opacity: 1;
}
.ant-table-tbody .tr-invoice:hover .popover-invoice-icon {
  opacity: 1;
}
.invoice-actions.ant-popover-placement-bottomRight,
.invoice-actions.ant-popover-placement-bottomLeft {
  padding: 0 !important;
}
.invoice-actions.ant-popover .ant-popover-inner {
  box-shadow: none;
  background: transparent;
}
.invoice-actions.ant-popover.ant-popover-content {
  left: 3px;
  position: relative;
}
.invoice-actions.ant-popover .ant-popover-inner,
.invoice-actions.ant-popover .ant-popover-arrow {
  top: -3px;
}
.invoice-actions.ant-popover .ant-popover-inner-content {
  padding: 20px 20px 5px 20px;
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  box-shadow: 0 5px 20px 0 rgba(13, 27, 42, 0.2);
  border-radius: 3px;
  margin-left: 12px;
}
.invoice-actions.ant-popover .ant-popover-inner-content .popover-row {
  text-align: left;
  display: flex;
  font-weight: 400;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
  align-items: center;
  margin-bottom: 15px;
  cursor: pointer;
}
.invoice-actions.ant-popover .ant-popover-inner-content .popover-row > span {
  flex: 1;
}
.invoice-actions.ant-popover .ant-popover-inner-content .popover-row svg {
  margin-right: 10px;
}
.invoice-actions #parentDatePicker {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.invoice-actions #parentDatePicker .form-item-date {
  display: none;
}
.invoice-actions #parentDatePicker .ant-calendar-picker-container {
  left: inherit !important;
  top: inherit !important;
  right: 0;
}
.invoice-actions #parentDatePicker .ant-calendar-picker-container .ant-calendar {
  margin-top: 0px;
}
