.user-menu {
  min-width: 250px;
}
.user-menu .user-menu-list {
  margin: 0 !important;
}
.user-menu .user-menu-list:first-child {
  padding: 7px 17px!important;
}
.user-menu .user-menu-list .ant-row-flex {
  line-height: 14px !important;
}
.user-menu .user-menu-list .ant-menu-item {
  padding: 0 !important;
  line-height: 14px !important;
}
.user-menu .user-menu-list .ant-menu-submenu-title {
  margin: 0 !important;
  padding: 7px 17px!important;
  line-height: 14px !important;
}
.user-menu .user-menu-list .ant-menu-submenu-title + .ant-menu-sub {
  padding: 7px 17px!important;
}
.user-menu .user-menu-list .ant-menu-submenu-title + .ant-menu-sub .ant-menu-item {
  padding: 7px 0px 7px !important;
  padding-left: 48px;
  margin: 0 !important;
  font-weight: 400;
  font-size: 14px;
  color: #0D1B2A;
  letter-spacing: 0;
  height: inherit;
}
.user-menu .user-menu-list .ant-menu-submenu-title + .ant-menu-sub .ant-menu-item .bold {
  font-weight: 600;
}
.user-menu .user-menu-item {
  line-height: 25px;
  font-weight: 400;
  font-size: 14px;
  color: #0d1b2a;
  letter-spacing: 0;
}
.user-menu .user-menu-item .with-bg {
  background: #0053FA;
  color: white;
}
.user-menu .user-menu-item .user-menu-icon {
  color: #0053FA;
  margin-right: 5px;
  vertical-align: middle;
}
.user-menu .header-solde-title .user-menu-item {
  font-weight: 500;
  font-size: 10px;
  color: #989CA8;
  letter-spacing: 0;
  text-transform: uppercase;
}
.user-menu .header-solde-title .header-solde {
  font-weight: 500;
  font-size: 15px;
  color: #0d1b2a;
  letter-spacing: 0;
  line-height: 21px;
}
.user-menu .user-menu-item-signout {
  background: #0053FA;
  color: white;
}
.user-menu .user-menu-item-signout .user-menu-item {
  color: white;
}
.user-menu .user-menu-item-signout .user-menu-item .user-menu-icon {
  color: white;
}
.user-menu .user-menu-caret {
  color: #989CA8;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: rotate(0deg) translate(0, -50%);
  width: 15px;
  height: 15px;
}
.user-menu.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected,
.user-menu.ant-menu-inline:not(.ant-menu-horizontal) .ant-menu-item-selected {
  background: inherit !important;
}
.user-menu.ant-menu .ant-menu-selected:after,
.user-menu.ant-menu-inline .ant-menu-selected:after,
.user-menu.ant-menu .ant-menu-item-selected:after,
.user-menu.ant-menu-inline .ant-menu-item-selected:after {
  display: none;
}
.user-menu .ant-menu-item-divider {
  height: inherit;
  margin: 0 17px !important;
}
.user-menu .ant-menu-submenu-arrow:before,
.user-menu .ant-menu-submenu-arrow:after {
  display: none;
}
.ant-menu-submenu-open .header-submenu-caret {
  transform: rotate(90deg) translate(-50%, 0%);
}
.user-menu .ant-menu-submenu > .ant-menu {
  background-color: #F5F6FA;
}
.user-menu .ant-menu-submenu > .ant-menu .ant-menu-item .with-list:before {
  content: '-';
  color: #0053FA;
  margin-right: 3px;
}
